import { Flex, Spacer } from '@chakra-ui/react'
import {Link} from 'react-router-dom'
import "./navbar.css"
export const Navbar=()=>{
return <div className='navbar' id="nav-menu">
<Link style={{ width:"8%" ,display:'flex',alignItems:"center",marginRight:"30%"}} className='link' to ='/'><img className="nav-link home" width="100%"src='https://i.postimg.cc/HLs13XS2/Whats-App-Image-2023-03-17-at-18-24-35.jpg'/></Link> 
<Link className='link' to ='/about'><h3 className="nav-link about" >About </h3></Link> 
<Link   className='link nav-link skills' to ='/skills'><h3 className="" >Skills</h3></Link>
<Link className='link nav-link projects' to ='/projects'><h3 className="">Project</h3></Link>
<Link className='link nav-link contact' to ='Contact'><h3 className="">Contact</h3></Link>
<Link className='link nav-link resume' to ='/resume'><h3 className="">Reusme</h3></Link>


 
</div>
}